*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
body{
    
  background: #ee9ca7; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ee9ca7, #ffdde1); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ee9ca7, #ffdde1);
}
.todo-app{
    display: flex;
    flex-direction: column;
    justify-self: start;
    width: 520px;
    min-height: 620px ;
    background: #20232a;
    border-radius: 5px;
    padding-bottom: 32px;
    text-align: center;
    margin: 128px auto;
}
h1 {
    margin: 32px 0;
    color: #fff;
    font-size: 24px;
  }
  .complete {
    text-decoration: line-through;
    opacity: 0.4;
  }
  .todo-form {
    margin-bottom: 32px;
  }
  .todo-input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #5d0cff;
    outline: none;
    width: 320px;
    background: transparent;
    color: #fff;
  }
  .todo-input::placeholder {
    color: #e2e2e2;
  }

  .todo-button {
    padding: 16px;
    border: none;
    border-radius: 0 25px 25px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
      90deg,
      rgba(93, 12, 255, 1) 0%,
      rgba(155, 0, 250, 1) 100%
    );
    color: #fff;
    text-transform: capitalize;
  }

  .todo-input.edit {
    border: 2px solid #DA36C6;
  }

  .todo-button.edit {
    background: linear-gradient(
      90deg,
      rgba(218, 54, 158) 0%,
      rgba(218, 54, 198) 100%
    );
    padding: 16px 22px;
  }
  .todo-container {
    display: flex;
    flex-direction: row;
    position: relative;
  }
  
  .todo-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4px auto;
    color: #fff;
    background: linear-gradient(
      90deg,
      rgba(255, 118, 20, 1) 0%,
      rgba(255, 84, 17, 1) 100%
    );
  
    padding: 16px;
    border-radius: 5px;
    width: 90%;
  }
  
  .todo-row:nth-child(4n + 1) {
    background: linear-gradient(
      90deg,
      rgba(93, 12, 255, 1) 0%,
      rgba(155, 0, 250, 1) 100%
    );
  }
  
  .todo-row:nth-child(4n + 2) {
    background: linear-gradient(
      90deg,
      rgba(255, 12, 241, 1) 0%,
      rgba(250, 0, 135, 1) 100%
    );
  }
  
  .todo-row:nth-child(4n + 3) {
    background: linear-gradient(
      90deg,
      rgba(20, 159, 255, 1) 0%,
      rgba(17, 122, 255, 1) 100%
    );
  }
   .todo-row:nth-child(5n + 1) {
    background: linear-gradient(
      90deg,
      rgba(42, 239, 144) 0%,
      rgba(42, 239, 144) 100%
    );
  } 
  .todo-row:nth-child(5n + 2) {
    background: linear-gradient(
      90deg,
      rgba(191, 239, 42) 0%,
      rgba(191, 239, 42) 100%
    );
  } 
  .todo-row:nth-child(5n + 3) {
    background: linear-gradient(
      90deg,
      rgba(114, 124, 109) 0%,
      rgba(114, 124, 109) 100%
    );
  } 
  /* .todo-row:nth-child(5n + 4) {
    background: linear-gradient(
      90deg,
      rgba(191, 239, 42) 0%,
      rgba(191, 239, 42) 100%
    );
  }  */
  .icons {
    display: flex;
    align-items: center;
    font-size: 24px;
    cursor: pointer;
  }
  
  .delete-icon {
    margin-right: 5px;
    color: #fff;
  }
  
  .edit-icon {
    color: #fff;
  }